<template>
  <div>
    <div class="block d-none d-lg-block" id="top"></div>
    <div class="container pt-5 text-center block about">
      <h1 class="mb-4">О Нас</h1>
      <p>
        Мы специализируемся на создании веб-проектов любого уровня сложности и масштаба.
        В нашей команде работают только творческие и сосредоточенные на цели люди.
        Благодаря тому что каждый из нас любит свое дело мы делаем качественные проекты, который кажутся намного дороже чем есть на самом деле, нам не нравится то что почти во всех студиях чтобы заказать даже простой сайт приходится платить огромные деньги, поэтому мы решили создать свою студию для того чтобы создать сайт была возможность у каждого.
      </p>
    </div>
    <main-form></main-form>
  </div>
</template>

<script>
  import MainForm from '../components/MainForm'
  export default {
    components: {
      MainForm
    },
    created() {
      window.scrollTo(0, 0);
    },
  }
</script>


